import {asyncComponent} from 'nc-lightapp-front';
import List from '../list';
const  Card = asyncComponent(()=>import(/* webpackChunkName: "uapbd/customer/custapply/card/Card" */'../card'));
const routes = [
    {
        path:'/',
        component:List,
        exact:true
    },
    {
        path:'/list',
        component:List
    },
    {
        path:'/card',
        component:Card
    }
    ]

export default routes;
